// ** React Imports
import { Fragment, useState, useEffect, useCallback, useMemo } from "react"

// ** Reactstrap Imports
import {
  Card,
  CardBody,
  CardHeader,
  CardTitle,
  Row,
  Col,
  Label,
  Form
} from "reactstrap"

import Select from "react-select"

import { useTranslation } from "react-i18next"

// ** Utils
import { selectThemeColors, getUserData } from "@utils"
// ** Store & Actions
import {
  getUnits,
  setUnitSelected,
  setUnitSelectedDropdown
} from "../views/apps/unit/store"
import { useDispatch, useSelector } from "react-redux"
import { useForm, Controller } from "react-hook-form"

const GlobalUnitDropdown = ({
  parentChange,
  colMode = false,
  colSize = 6,
  initialValue = true,
  colClassName = ""
}) => {
  const { t } = useTranslation()
  const userData = getUserData()
  const dispatch = useDispatch()
  const store = useSelector((state) => state.units)
  const [unitOptions, setUnitOptions] = useState([])
  const [currentUnit, setCurrentUnit] = useState(
    initialValue
      ? {
          value: "",
          label: `${t("Select")} ${t("Unit")}`
        }
      : {}
  )

  const handleChange = (data, parentChange) => {
    setCurrentUnit(data)
    // localStorage.setItem("last_id_unit", data.value)
    if (typeof parentChange === "function") {
      parentChange(data)
    }
  }

  useEffect(() => {
    // if (store.data.length === 0 && !store.isLoading) {
    //   dispatch(getUnits())
    // }
    // console.log(userData.member_of)
    const unitOptions = initialValue
      ? store.dataDropdown
      : store.dataDropdown.filter((item) => item.value !== "")

    setUnitOptions(unitOptions)
    setCurrentUnit(initialValue ? store.selectedUnit : unitOptions[0])
  }, [dispatch, store.dataDropdown])

  return store.data?.length > 1 ? (
    !colMode ? (
      <Card>
        {/* <CardHeader>
        <CardTitle tag="h4">{t("Filters")}</CardTitle>
      </CardHeader> */}
        <CardBody>
          <Row>
            <Label className="" for="plan-select">
              {t("Unit")}
            </Label>
            <Select
              theme={selectThemeColors}
              isClearable={false}
              className="react-select"
              classNamePrefix="select"
              options={unitOptions}
              value={currentUnit}
              onChange={(e) => {
                handleChange(e, parentChange)
              }}
              getOptionLabel={(option) =>
                option.code
                  ? `${option.code} ${option.label}`
                  : `${option.label}`
              }
            />
          </Row>
        </CardBody>
      </Card>
    ) : (
      <Col md={`${colSize}`} className={colClassName}>
        <Label className="" for="plan-select">
          {t("Unit")}
        </Label>
        <Select
          theme={selectThemeColors}
          isClearable={false}
          className="react-select"
          classNamePrefix="select"
          options={unitOptions}
          value={currentUnit}
          onChange={(e) => {
            handleChange(e, parentChange)
          }}
          getOptionLabel={(option) =>
            option.code ? `${option.code} ${option.label}` : `${option.label}`
          }
          // styles={{ menu: (base) => ({ ...base, base.children fontWeight: "bold" }) }}
        />
      </Col>
    )
  ) : null
}

export default GlobalUnitDropdown
